import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { NewCategoryService } from '../../angular-services/new-category.service';

@Component({
  selector: 'app-new-news-category-form-admin',
  templateUrl: './new-news-category-form.component.html',
  styleUrls: ['./new-news-category-form.component.css']
})
export class NewNewsCategoryFormComponent implements OnInit {

  formNewCategory: FormGroup;
  alert: any = { type: '', message: '', show: false };

  constructor(private newCategoryService: NewCategoryService) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.formNewCategory = new FormGroup({
      title: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  save() {
    if (this.formNewCategory.valid) {
      let data = this.formNewCategory.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.newCategoryService.addCategory(data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

}
