import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewNewsCategoryPageComponent } from './new-news-category-page.component';

const routes: Routes = [
  { path: '', component: NewNewsCategoryPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewNewsCategoryPageRoutingModule { }
